import { Toggle } from "./react-connections-game/src/components/ui/toggle";
import { useNavigate } from 'react-router-dom';

function Root() {
	let navigate = useNavigate();
	function move(path) {
		navigate(path);
	}
	return (
		<div id="sidebar">
		<h1 className="font-space-mono">Games</h1>
		<div class="grid grid-rows-1 grid-cols-3 gap-4">
		<Toggle onClick={() => move("/connections")}>
		Connections
		</Toggle>
		<Toggle>Wordle</Toggle>
		<Toggle>CrossWord</Toggle>
		</div>
		</div>
	);
}
export default Root;
